@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-VariableFont_wght.ttf");
  font-style: normal;
}
.pass-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.pass-input__label {
  margin-bottom: 4px;
  color: #5c5c5c;
  font-size: 12px;
  line-height: 1.33;
}
.pass-input__input {
  padding: 7px 34px 7px 8px;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.16);
  background: #fff;
  color: #1f1f1f;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: all 0.2s;
}
.pass-input__input::placeholder {
  color: #8f8f8f;
}
.pass-input__input:hover {
  border-color: rgba(1, 144, 219, 0.48);
}
.pass-input__input:focus {
  outline: none;
  border-color: rgba(1, 144, 219, 0.48);
}
.pass-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}
.pass-input__input.error {
  border-color: rgba(255, 59, 56, 0.48);
}
.pass-input__show {
  position: absolute;
  display: flex;
  bottom: 6px;
  right: 8px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.pass-input__show:hover svg {
  fill: #1f1f1f;
}
.pass-input__show.active {
  display: flex;
}
.pass-input__icon {
  fill: #8f8f8f;
  transition: all 0.2s;
}
.pass-input__icon_open {
  width: 20px;
}
.pass-input__alert {
  display: none;
  margin-top: 4px;
  color: #ff3b38;
  font-size: 14px;
  line-height: 20px;
}
.pass-input__alert.active {
  display: block;
}

.text-area {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-area__label {
  margin-bottom: 4px;
  color: rgba(39, 38, 44, 0.72);
  font-size: 12px;
  line-height: 1.33;
}
.text-area__input {
  resize: none;
  height: 100%;
  padding: 16px 20px;
  border-radius: 16px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background: rgb(255, 255, 255);
  color: rgb(39, 38, 44);
  font-family: inherit;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  transition: all 0.2s;
}
.text-area__input::placeholder {
  color: rgba(39, 38, 44, 0.56);
}
.text-area__input:hover {
  border-color: rgba(0, 0, 0, 0.48);
}
.text-area__input:focus {
  outline: none;
  border-color: rgb(39, 191, 171);
}
.text-area__input:disabled {
  border-color: rgba(0, 0, 0, 0.08);
  background-color: rgba(0, 0, 0, 0.08);
}

.text-input {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__label {
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.33;
}
.text-input__input {
  min-height: 44px;
  padding: 10px 34px 10px 16px;
  border-radius: 16px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  color: inherit;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  transition: all 0.2s;
}
.text-input__input::placeholder {
  color: rgba(39, 38, 44, 0.56);
}
.text-input__input:hover {
  border-color: rgba(0, 0, 0, 0.48);
}
.text-input__input:focus {
  outline: none;
  border-color: rgb(39, 191, 171);
}
.text-input__input:disabled {
  border-color: rgba(0, 0, 0, 0.16);
  opacity: 0.64;
}
.text-input__input.error {
  border-color: rgb(229, 24, 24);
}
.text-input__input_force-alert {
  border-color: rgb(229, 24, 24);
}
.text-input__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
}
.text-input__reset {
  display: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.text-input__reset:hover svg {
  color: rgb(39, 38, 44);
}
.text-input__reset.active {
  display: flex;
}
.text-input__icon {
  color: rgb(39, 38, 44);
  transition: all 0.2s;
}
.text-input__alert {
  display: none;
  margin-top: 4px;
  color: rgb(229, 24, 24);
  font-size: 14px;
  line-height: 20px;
}
.text-input__alert.active {
  display: block;
}
.text-input__sub-text {
  margin-top: 4px;
  color: rgba(39, 38, 44, 0.72);
  font-size: 14px;
  line-height: 20px;
}
.text-input__force-alert {
  margin-top: 4px;
  color: rgb(229, 24, 24);
  font-size: 14px;
  line-height: 20px;
}

.tooltip {
  position: absolute;
  top: -50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  z-index: 900;
  width: max-content;
  padding: 12px 24px;
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.48);
  font-size: 12px;
  line-height: 1.33;
  color: rgb(255, 255, 255);
  opacity: 0;
}
.tooltip__arrow {
  position: absolute;
  left: 14px;
  bottom: -6px;
  width: 12px;
  height: 6px;
  fill: rgba(0, 0, 0, 0.48);
}

.tooltip.tooltip_left {
  left: auto;
  right: 0;
}
.tooltip.tooltip_left .tooltip__arrow {
  left: auto;
  right: 14px;
}

.tooltip.tooltip_bottom {
  top: auto;
  bottom: 0;
  transform: translateY(calc(100% + 6px));
}
.tooltip.tooltip_bottom .tooltip__arrow {
  transform: rotate(180deg);
  bottom: auto;
  top: -6px;
}

.tooltip-trigger {
  position: relative;
}

.tooltip-trigger:hover .tooltip {
  visibility: visible;
  opacity: 1;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  max-width: 1176px;
  min-height: 92px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 12px;
  background-color: rgb(255, 255, 255);
}
.header__logo {
  width: 154px;
  height: 44px;
  margin-right: 12px;
}
.header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 48px;
}
.header__content-question {
  display: flex;
  align-items: center;
}
.header__content-question-icon {
  flex-shrink: 0;
  width: 26px;
  height: 26px;
  margin-right: 15px;
  color: rgb(39, 191, 171);
}
.header__content-question-button {
  display: inline;
  padding: 0;
  margin: 0 0 0 5px;
  background-color: transparent;
  border: none;
  outline: none;
  font-weight: 500;
  color: rgb(17, 130, 187);
  transition: color 0.2s;
  cursor: pointer;
}
.header__content-question-button:hover {
  color: rgb(12, 98, 141);
}
.header__content-account {
  display: flex;
  align-items: center;
}
.header__content-account-text {
  display: flex;
  align-items: baseline;
  gap: 4px;
}
.header__content-account-name {
  display: inline-block;
  max-width: 400px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-wrap: nowrap;
}
.header__exit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin: 0 0 0 24px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__exit-button-icon {
  width: 24px;
  height: 24px;
  color: rgba(39, 38, 44, 0.72);
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border-width: 2px;
  border-style: solid;
  border-radius: 16px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button.s {
  padding: 7px 23px;
}
.button.m {
  padding: 11px 14px;
}
.button.l {
  padding: 15px 31px;
  font-size: 16px;
}
.button.primary {
  border-color: rgb(39, 191, 171);
  background-color: rgb(39, 191, 171);
  color: rgb(255, 255, 255);
}
.button.primary:hover:not(:disabled) {
  border-color: rgb(26, 127, 114);
}
.button.primary:active:not(:disabled) {
  border-color: rgb(39, 191, 171);
  background-color: rgb(26, 127, 114);
}
.button.primary:disabled {
  border-color: rgb(39, 191, 171);
  background-color: rgb(39, 191, 171);
  opacity: 0.48;
}
.button.secondary {
  background-color: transparent;
  border-color: rgba(39, 38, 44, 0.72);
  color: rgb(39, 38, 44);
}
.button.secondary:hover:not(:disabled) {
  border-color: rgb(39, 191, 171);
  background-color: rgba(0, 0, 0, 0.04);
}
.button.secondary:active:not(:disabled) {
  border-color: rgb(39, 191, 171);
  background-color: rgb(26, 127, 114);
  color: rgb(255, 255, 255);
}
.button.secondary:disabled {
  border-color: rgba(39, 38, 44, 0.72);
  opacity: 0.48;
}

.toggle {
  display: flex;
  align-items: center;
  padding: 8px 0;
}
.toggle input[type=checkbox] {
  display: none;
}
.toggle input[type=checkbox]:checked + label {
  color: rgb(39, 38, 44);
}
.toggle input[type=checkbox]:checked + label::before {
  border-color: rgba(0, 0, 0, 0.16);
  background-color: rgb(39, 191, 171);
}
.toggle input[type=checkbox]:checked + label::after {
  transform: translateX(20px);
  background-color: rgb(255, 255, 255);
}
.toggle input[type=checkbox].error + label::before {
  border-color: rgb(229, 24, 24);
}
.toggle input[type=checkbox].error + label .radio__description {
  color: rgb(229, 24, 24);
}
.toggle__label {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding-left: 60px;
  margin: 4px 0;
  color: rgba(39, 38, 44, 0.72);
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
  user-select: none;
  transition: color 0.2s;
}
.toggle__label:hover {
  color: rgb(39, 38, 44);
}
.toggle__label:hover::before {
  border-color: rgb(39, 191, 171);
}
.toggle__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: -4px;
  width: 48px;
  height: 28px;
  border-radius: 99px;
  border: 2px solid rgba(0, 0, 0, 0.16);
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;
  transition: all 0.2s;
}
.toggle__label::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.24);
  transition: all 0.2s ease;
}
.toggle__description {
  color: rgba(39, 38, 44, 0.72);
  font-size: 14px;
  line-height: 20px;
}

