* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;
  font-family: "Rubik", "Helvetica", sans-serif;
  line-height: 1.5;
  color: rgb(39, 38, 44);
  background-color: rgb(255, 255, 255);
}

.main {
  max-width: 1176px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  padding: 0 8px;
  margin: 92px auto 48px;
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 44px;
  line-height: 56px;
  letter-spacing: -0.96px;
}

.sub-title {
  margin: 0;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
}

.message {
  display: flex;
