  color: rgb(30, 174, 83);
}
.message__form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 24px;
}
.message__textarea {
  height: 104px;
}
.message__controls {
  display: flex;
  gap: 12px;
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}